<template>
  <div>
    <v-container
      fluid
      grid-list-md
    >
      <v-card>
        <v-toolbar dense>
          <v-toolbar-title></v-toolbar-title>
          <v-spacer></v-spacer>
          <file-upload-icon
            tooltip=""
            @onFileRead="onFileRead"
          />
          <v-tooltip top>
            <v-btn
              icon
              small
              slot="activator"
              @click="saveSpellData"
            >
              <v-icon>save</v-icon>
            </v-btn>
            <span></span>
          </v-tooltip>
          <file-download-icon
            tooltip=""
            :data="jesonspelldata"
          />
          <v-tooltip top>
            <v-btn
              icon
              small
              slot="activator"
              @click="loadSpellData"
            >
              <v-icon>redo</v-icon>
            </v-btn>
            <span></span>
          </v-tooltip>
          <v-tooltip top>
            <v-btn
              icon
              small
              slot="activator"
              @click="deleteSpellData"
            >
              <v-icon>delete</v-icon>
            </v-btn>
            <span></span>
          </v-tooltip>
          <v-tooltip top>
            <v-btn
              icon
              small
              slot="activator"
              @click="addSpell"
            >
              <v-icon>add</v-icon>
            </v-btn>
            <span></span>
          </v-tooltip>
          <v-tooltip top>
            <v-btn
              icon
              small
              slot="activator"
              @click="showHelp"
            >
              <v-icon>help</v-icon>
            </v-btn>
            <span></span>
          </v-tooltip>
        </v-toolbar>

        <!---->
        <!---->
        <v-layout
          v-if="!isMobile"
          row
          wrap
          justify-center
        >
          <v-flex xs1>
          </v-flex>
          <v-flex xs3>
            <v-text-field
              append-icon="search"
              row-height="12"
              label="Input"
              single-line
              v-model="conditon.spellname"
              hint=""
              persistent-hint
            ></v-text-field>
          </v-flex>
          <v-flex xs1>
            <v-select
              dense
              label="Select"
              :items="levels"
              v-model="conditon.levels"
              multiple
              max-height="400"
              hint=""
              persistent-hint
            ></v-select>
          </v-flex>
          <v-flex xs1>
            <v-select
              dense
              label="Select"
              :items="casting_time"
              v-model="conditon.casting_time"
              max-height="400"
              hint=""
              persistent-hint
            ></v-select>
          </v-flex>
          <v-flex xs2>
            <v-select
              dense
              label="Select"
              :items="classes"
              v-model="conditon.classes"
              multiple
              max-height="400"
              hint=""
              persistent-hint
            ></v-select>
          </v-flex>
          <v-flex xs2>
            <v-select
              dense
              label="Select"
              :items="classes"
              v-model="conditon.exlcudedClasses"
              multiple
              max-height="400"
              hint="("
              persistent-hint
            ></v-select>
          </v-flex>
          <v-flex xs1>
            <v-select
              dense
              label="Select"
              :items="sources"
              v-model="conditon.sources"
              multiple
              max-height="400"
              hint=""
              persistent-hint
            ></v-select>
          </v-flex>
          <v-flex xs1>
          </v-flex>
          <v-flex xs1>
          </v-flex>
          <v-flex xs3>
            <v-text-field
              dense
              append-icon="search"
              row-height="12"
              label="Input"
              single-line
              v-model="conditon.desc"
              hint="|OR"
              persistent-hint
            ></v-text-field>
          </v-flex>
          <v-flex xs2>
            <v-select
              dense
              label="Select"
              :items="subclassses"
              v-model="conditon.subclassses"
              multiple
              max-height="400"
              hint=""
              persistent-hint
            ></v-select>
          </v-flex>
          <v-flex xs1>
            <v-select
              dense
              label="Select"
              :items="schools"
              v-model="conditon.school"
              max-height="400"
              hint=""
              persistent-hint
            ></v-select>
          </v-flex>
          <v-flex xs1>
            <v-select
              dense
              label="Select"
              :items="concentration"
              v-model="conditon.concentration"
              max-height="400"
              hint=""
              persistent-hint
            ></v-select>
          </v-flex>
          <v-flex xs1>
            <v-select
              dense
              :items="conditonRituals"
              v-model="conditon.ritual"
              max-height="400"
              hint=""
              persistent-hint
            ></v-select>
          </v-flex>
          <v-flex xs1>
            <v-select
              dense
              label="Select"
              :items="components"
              v-model="conditon.components"
              multiple
              max-height="400"
              hint=""
              persistent-hint
            ></v-select>
          </v-flex>
          <v-flex xs1>
            <v-select
              dense
              label="Select"
              :items="allTags"
              v-model="conditon.tags"
              item-text="name"
              item-value="self"
              multiple
              max-height="400"
              hint=""
              persistent-hint
            ></v-select>
          </v-flex>
          <v-flex xs1>
          </v-flex>
        </v-layout>
        <!---->
        <v-data-table
          v-if="!isMobile"
          :headers="headers"
          :items="items"
          item-key="name"
          no-data-text=""
        >
          <template
            slot="items"
            slot-scope="props"
          >
            <tr
              @click="clickCell(props.item)"
              @click.right.prevent="rightClickCell(props.item)"
            >
              <td class="
              text-xs-left">{{ formatSpellName(props.item) }} </td>
              <td class="text-xs-left">{{ props.item.hoge }} {{ props.item.level}}</td>
              <td class="text-xs-left">{{ props.item.formatArray(props.item.components,components) }}</td>
              <td class="text-xs-left nowrap">{{ props.item.casting_time }}</td>
              <td class="text-xs-left">{{ props.item.formatDuration }}</td>
              <td class="text-xs-left">{{ props.item.range }}</td>
              <td class="text-xs-left">{{ props.item.source }}</td>
              <td class="text-xs-left">
                <v-icon>add</v-icon>
              </td>
            </tr>
          </template>
        </v-data-table>
        <!---->

        <!---->
        <!---->
        <v-layout
          v-if="isMobile"
          row
          wrap
        >
          <v-flex
            xs10
            offset-xs1
          >
            <v-text-field
              append-icon="search"
              label="Input"
              single-line
              v-model="conditon.spellname"
              hint=""
              persistent-hint
            ></v-text-field>
          </v-flex>
          <v-flex
            xs6
            offset-xs3
          >
            <v-btn @click="showMobileSearchDetailDialog=true">
              <v-icon>settings</v-icon> 
            </v-btn>
          </v-flex>
        </v-layout>
        <!---->
        <v-list
          v-if="isMobile"
          dense
          two-line
        >
          <v-data-iterator
            content-tag="v-card"
            :items="items"
          >
            <v-list-tile
              avatar
              slot="item"
              slot-scope="props"
              @click="clickCell(props.item)"
            >
              <v-list-tile-content>
                <v-list-tile-title>{{props.item.name}} </v-list-tile-title>
                <v-list-tile-sub-title>{{props.item.level}}/{{props.item.format(props.item.school,schools)}}/{{props.item.formatArray(props.item.components,components)}}</v-list-tile-sub-title>
              </v-list-tile-content>
              <v-list-tile-avatar>
                <v-icon>add</v-icon>
              </v-list-tile-avatar>
            </v-list-tile>
          </v-data-iterator>
        </v-list>

        <!---->

        <v-card-text>2018.09.02 minokuba</v-card-text>
      </v-card>
      <v-card>
        <ins
          class="adsbygoogle"
          style="display:block"
          data-ad-client="ca-pub-9097509632200457"
          data-ad-slot="5985300299"
          data-ad-format="auto"
        ></ins>
      </v-card>
    </v-container>
    <v-snackbar
      top
      :color="snackbar.level"
      :timeout="3000"
      v-model="snackbar.show"
    >
      {{ snackbar.message }}
    </v-snackbar>
    <spell-detail-dialog
      :showEditDialog.sync="showEditDialog"
      v-bind:targetSpell="targetSpell"
      v-bind:createSpell="createSpell"
      @save="save"
      @remove="remove"
      @cancel="cancelOrClose"
      @close="cancelOrClose"
    />
    <spell-help-dialog :showDialog.sync="showHelpDialog"></spell-help-dialog>
    <add-tag-dialog
      :showDialog.sync="showAddTagDialog"
      :tags.sync="allTags"
      :spellname="addTagSpellName"
      @save="saveTags"
      @cancel="cancelAddTags"
    />

    <v-dialog
      v-if="isMobile"
      fullscreen
      v-model="showMobileSearchDetailDialog"
    >
      <v-card>
        <v-card-title>
          <H2></H2>
        </v-card-title>
        <v-card>
          <v-layout
            row
            wrap
          >
            <v-flex
              xs10
              offset-xs1
            >
              <v-select
                label="Select"
                hide-selected
                dense
                :items="levels"
                v-model="conditon.levels"
                multiple
                max-height="200"
                hint=""
                persistent-hint
              ></v-select>
            </v-flex>
            <v-flex
              xs10
              offset-xs1
            >
              <v-select
                label="Select"
                hide-selected
                dense
                :items="classes"
                v-model="conditon.classes"
                multiple
                max-height="200"
                hint=""
                persistent-hint
              ></v-select>
            </v-flex>
            <!--v-flex xs10 offset-xs1>
              <v-select label="Select" hide-selected dense :items="subclassses" v-model="conditon.subclassses" multiple max-height="200" hint="" persistent-hint></v-select>
            </v-flex-->
            <v-flex
              xs10
              offset-xs1
            >
              <v-select
                :items="conditonRituals"
                dense
                v-model="conditon.ritual"
                max-height="200"
                hint=""
                persistent-hint
              ></v-select>
            </v-flex>
            <v-flex
              xs10
              offset-xs1
            >
              <v-select
                label="Select"
                dense
                :items="components"
                v-model="conditon.components"
                multiple
                max-height="200"
                hint=""
                persistent-hint
              ></v-select>
            </v-flex>
            <v-flex
              xs10
              offset-xs1
            >
              <v-select
                label="Select"
                dense
                :items="casting_time"
                v-model="conditon.casting_time"
                max-height="200"
                hint=""
                persistent-hint
              ></v-select>
            </v-flex>
            <!--v-flex xs10 offset-xs1>
              <v-select label="Select" dense :items="schools" v-model="conditon.school" max-height="200" hint="" persistent-hint></v-select>
            </v-flex-->
          </v-layout>
        </v-card>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="green darken-1"
            flat
            @click.native="closeMobileSearchDetailDialog"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>

</template>


<style>
/*table-layout: fixed;  text-overflow: ellipsis; ...
 * http://chuckwebtips.hatenablog.com/entry/2017/07/09/183527 
 */
td.nowrap {
  max-width: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
}
</style>

<script>
import spells from "@/model/spells";
import constants from "@/model/constants";
import Spell from "@/model/spell";
import Tag from "@/model/tag";

import FileUploadIcon from "@/components/FileUploadIcon";
import FileDownloadIcon from "@/components/FileDownloadIcon";
import SpellDetailDialog from "@/components/SpellDetailDialog";
import SpellHelpDialog from "@/components/SpellHelpDialog";
import AddTagDialog from "@/components/AddTagDialog";

export default {
  name: "SearchSpellTable",
  components: {
    FileUploadIcon: FileUploadIcon,
    FileDownloadIcon: FileDownloadIcon,
    SpellDetailDialog: SpellDetailDialog,
    SpellHelpDialog: SpellHelpDialog,
    AddTagDialog: AddTagDialog
  },
  mounted() {
    (window.adsbygoogle = window.adsbygoogle || []).push({});
  },
  data() {
    return {
      //
      spelldata: [],
      //
      conditon: {
        spellname: "",
        desc: "",
        levels: [],
        classes: [],
        exlcudedClasses: [],
        ritual: null,
        components: [],
        casting_time: "",
        concentration: null,
        school: null,
        subclassses: [],
        sources: [],
        tags: []
      },
      //
      snackbar: {
        show: false,
        level: "info",
        message: ""
      },
      //
      levels: constants.levels,
      classes: constants.classes,
      components: constants.components,
      concentration: constants.concentration,
      schools: constants.schools,
      casting_time: constants.casting_time,
      subclassses: constants.subclassses,
      sources: constants.sources,
      headers: [
        { text: "", value: "name", align: "left", width: "30%" },
        { text: "", value: "level", align: "left", width: "5%" },
        { text: "", value: "components", align: "left", width: "20%" },
        {
          text: "",
          value: "casting_time",
          align: "left",
          width: "10%"
        },
        { text: "", value: "duration", align: "left", width: "10%" },
        { text: "", value: "range", align: "left", width: "10%" },
        { text: "", value: "source", align: "left", width: "10%" },
        { text: " ", value: "dummy", align: "left", width: "5%" }
      ],
      conditonRituals: [
        { text: "", value: null },
        { text: "", value: "yes" },
        { text: "", value: "no" }
      ],
      allTags: [],
      //
      showEditDialog: false,
      //
      createSpell: false,
      //
      targetSpell: null,
      //
      showHelpDialog: false,
      //
      showMobileSearchDetailDialog: false,
      //
      showAddTagDialog: false,
      //
      addTagSpellName: null
    };
  },
  beforeMount() {
    //1.
    var spellsjson = localStorage.getItem("dndapp.spells");
    //2.
    if (spellsjson != undefined && spellsjson != null) {
      //
      this.spelldata = Spell.assigns(JSON.parse(spellsjson));
    } else {
      //2.
      this.spelldata = Spell.assigns(spells());
    }

    this.allTags = Tag.load();
  },
  computed: {
    //
    items() {
      return (
        this.spelldata
          // .slice(1, 10) //for debug.
          .filter(element => {
            if (!this.filterDesc(element)) {
              return false;
            }
            if (!this.filterSpellname(element)) {
              return false;
            }
            if (!this.fliterRitual(element)) {
              return false;
            }
            if (!this.fliterLevels(element)) {
              return false;
            }
            //
            if (!this.filterClassOrSubclass(element)) {
              return false;
            }
            if (!this.fliterComopnents(element)) {
              return false;
            }
            if (!this.filterCastingTime(element)) {
              return false;
            }
            if (!this.filterConcentration(element)) {
              return false;
            }
            if (!this.filterSchool(element)) {
              return false;
            }
            if (!this.fliterSources(element)) {
              return false;
            }
            if (!this.filterTags(element)) {
              return false;
            }
            return true;
          })
      );
    },
    //
    jesonspelldata() {
      return JSON.stringify(this.spelldata);
    },
    //
    //(/DOMv-ifDOM
    // watch)
    isMobile() {
      const mobileBreakpoints = ["xs", "sm"];
      return mobileBreakpoints.some(e => {
        return this.$vuetify.breakpoint.name === e;
      });
    }
  },

  methods: {
    //computed
    fliterRitual(element) {
      if (this.conditon.ritual === null) {
        return true;
      } else {
        return element.ritual === this.conditon.ritual;
      }
    },
    fliterLevels(element) {
      if (this.conditon.levels.length === 0) {
        return true;
      } else {
        return this.conditon.levels.some(e => {
          return e === element.level;
        });
      }
    },
    fliterSources(element) {
      if (this.conditon.sources.length === 0) {
        return true;
      } else {
        return this.conditon.sources.some(e => {
          return e === element.source;
        });
      }
    },

    filterClassOrSubclass(element) {
      //1.
      //2.
      //3.
      //4.OR

      let skipFilterClasses =
        this.conditon.classes.length === 0 &&
        this.conditon.exlcudedClasses.length === 0;
      let skipFilterSubclasses = this.conditon.subclassses.length === 0;
      if (skipFilterClasses && skipFilterSubclasses) {
        return true;
      } else {
        if (skipFilterSubclasses) {
          return this.containsClasses(element);
        } else if (skipFilterClasses) {
          return this.containsSubclasses(element);
        } else {
          return (
            this.containsClasses(element) || this.containsSubclasses(element)
          );
        }
      }
    },

    contains(collection, item) {
      if (collection.length === 0) {
        return true;
      } else {
        return collection.some(e => {
          return item.includes(e);
        });
      }
    },

    doesNotContains(collection, item) {
      if (collection.length === 0) {
        return true;
      } else {
        return !collection.some(e => {
          return item.includes(e);
        });
      }
    },

    containsSubclasses(element) {
      return this.conditon.subclassses.some(e => {
        return element.subclass.includes(e);
      });
    },

    containsClasses(element) {
      // //1.
      if (!this.contains(this.conditon.classes, element.class)) {
        return false;
      }
      // // //2.
      if (!this.doesNotContains(this.conditon.exlcudedClasses, element.class)) {
        return false;
      }
      return true;
    },

    fliterComopnents(element) {
      if (this.conditon.components.length === 0) {
        return true;
      } else {
        if (element.components.length !== this.conditon.components.length) {
          return false;
        }
        for (const s of element.components) {
          if (this.conditon.components.indexOf(s) === -1) {
            return false;
          }
        }
        return true;
      }
    },
    filterSpellname(element) {
      var trimed = this.conditon.spellname.trim();
      if (trimed === "") {
        return true;
      } else {
        //FIXME includes
        return element.name.includes(trimed);
        //return element.name.startsWith(trimed);
      }
    },
    //FIXME constants
    filterCastingTime(element) {
      if (this.conditon.casting_time === null) {
        return true;
      } else {
        if (this.conditon.casting_time !== "") {
          return element.casting_time.startsWith(this.conditon.casting_time);
        } else {
          let result = !(
            element.casting_time.startsWith("1") ||
            element.casting_time.startsWith("1") ||
            element.casting_time.startsWith("1")
          );
          return result;
        }
      }
    },

    filterConcentration(element) {
      if (this.conditon.concentration === null) {
        return true;
      } else {
        return this.conditon.concentration === element.concentration;
      }
    },
    filterSchool(element) {
      if (this.conditon.school === null) {
        return true;
      } else {
        return element.school === this.conditon.school;
      }
    },
    //
    filterDesc(element) {
      if (this.conditon.desc === null || this.conditon.desc.trim() === "") {
        return true;
      } else {
        let niniJouken = [];
        let hissuJoken = [];
        var regex = new RegExp("[ |]?[^ ]+", "g");
        var word;
        let desc = this.conditon.desc.replace("", " ").trim();
        // alert(desc);

        while ((word = regex.exec(desc))) {
          let str = word[0].trim();
          if (str.startsWith("|")) {
            Array.push(niniJouken, str.substring(1));
          } else {
            Array.push(hissuJoken, str);
          }
        }

        // alert(JSON.stringify(niniJouken));
        // alert(JSON.stringify(hissuJoken));
        //1.
        let retVal;
        if (hissuJoken.length === 0) {
          retVal = false;
        } else {
          retVal = hissuJoken.every(desc => {
            return element.desc.includes(desc);
          });
        }

        if (retVal) {
          return true;
          //2.
        } else {
          return niniJouken.some(desc => {
            return element.desc.includes(desc);
          });
        }
      }
    },
    filterTags(element) {
      if (this.conditon.tags.length === 0) {
        return true;
      } else {
        return this.conditon.tags.some(tag => {
          return tag.contains(element.name);
        });
      }
    },

    //JSON
    onFileRead(files) {
      for (const file of files) {
        let fileReader = new FileReader();
        fileReader.onload = data => {
          let json = data.target.result;
          try {
            let spells = JSON.parse(json);
            this.spelldata = Spell.assigns(spells);
            this.showSnackbar(
              "success",
              ""
            );
          } catch (e) {
            //FIXME
            this.showSnackbar("error", "");
            this.console.log(e);
          }
        };
        fileReader.readAsText(file);
      }
    },
    //expand
    clickCell(item) {
      // props.expanded = !props.expanded;
      this.targetSpell = item;
      this.createSpell = false;
      this.showEditDialog = true;
    },
    rightClickCell(item) {
      this.addTagSpellName = item.name;
      this.showAddTagDialog = true;
    },
    //
    addSpell() {
      this.targetSpell = null;
      this.createSpell = true;
      this.showEditDialog = true;
    },
    // //
    save(result) {
      if (this.targetSpell == null) {
        this.spelldata.push(result);
        this.showSnackbar("success", "" + result.name + "");
      } else {
        Object.assign(this.targetSpell, result);
        this.targetSpell = null;
        this.showSnackbar("success", "" + result.name + "");
      }
      this.createSpell = null;
    },
    // //
    remove() {
      let index = this.spelldata.indexOf(this.targetSpell);
      this.spelldata.splice(index, 1);
      this.targetSpell = null;
      this.showSnackbar(
        "success",
        ""
      );
    },

    saveTags(updatedAllTags) {
      this.allTags = updatedAllTags;
      Tag.save(this.allTags);
      this.showAddTagDialog = false;
    },
    cancelAddTags() {
      this.addTagSpellName = null;
      this.showAddTagDialog = false;
    },
    // 
    cancelOrClose() {
      this.targetSpell = null;
      this.createSpell = null;
    },

    showHelp() {
      this.showHelpDialog = true;
    },

    //
    saveSpellData() {
      localStorage.setItem("dndapp.spells", this.jesonspelldata);
      this.showSnackbar(
        "success",
        ""
      );
    },

    //
    loadSpellData() {
      var spellsFromStorage = localStorage.getItem("dndapp.spells");
      //
      if (spellsFromStorage != undefined && spellsFromStorage != null) {
        this.spelldata = Spell.assigns(JSON.parse(spellsFromStorage));
        this.showSnackbar(
          "success",
          ""
        );
      } else {
        this.showSnackbar(
          "error",
          ""
        );
      }
    },

    //
    deleteSpellData() {
      localStorage.removeItem("dndapp.spells");
      this.spelldata = [];
      //this.spelldata = spells(); //
      this.showSnackbar(
        "success",
        ""
      );
    },

    formatSpellName(item) {
      var retVal = item.name + " " + item.format(item.school, this.schools);
      if (item.ritual === "yes") {
        retVal += "";
      }
      retVal += ")";
      return retVal;
    },

    showSnackbar(level, message) {
      this.snackbar.level = level;
      this.snackbar.message = message;
      this.snackbar.show = true;
    },

    closeMobileSearchDetailDialog() {
      this.showMobileSearchDetailDialog = false;
    }
  }
};
</script>



